	module.exports = {"data":{"markdownRemark":{"html":"<p>When I moved for my new job, I decided to cut the cord and only purchase an internet connection. But, of course, being online as much as I am, I wanted the fastest I could get. Originally, I was going to go with Google Fiber, but they aren’t yet available in my area. So I had to go with AT&#x26;T.</p>\n<h2>A Job-Aid Created Just for Me</h2>\n<p>Set up was a bit of a pain because, you know, I’m at work and they need someone at home on the day of the installation. But I got through that with a visit from my mum (hi, mum!) and things have been going smoothly. Then I got an email from them with information about my first month’s bill. This wasn’t just a “pay up” sort of notification. AT&#x26;T had actually created a personalized video that walked me through the charges on my first month’s bill.</p>\n<figure>\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75.37473233404711%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"ATT video for understanding my bill\" title=\"\" src=\"/static/Screen-Shot-2015-10-15-at-9.15.20-PM-6fca03b403bf879760cfbb7471f1f1a4-1626e.png\" srcset=\"/static/Screen-Shot-2015-10-15-at-9.15.20-PM-6fca03b403bf879760cfbb7471f1f1a4-076ec.png 203w,\n/static/Screen-Shot-2015-10-15-at-9.15.20-PM-6fca03b403bf879760cfbb7471f1f1a4-7ae34.png 405w,\n/static/Screen-Shot-2015-10-15-at-9.15.20-PM-6fca03b403bf879760cfbb7471f1f1a4-1626e.png 810w,\n/static/Screen-Shot-2015-10-15-at-9.15.20-PM-6fca03b403bf879760cfbb7471f1f1a4-5a73c.png 934w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n</figure>\n<p>When you first get services set up, things can often be a bit confusing because there are all of these surcharges and one-time fees. My previous ISP, Suddenlink, sent bills with no charge explanations at all (which is why I ended up cutting them back to just internet, as well). But this video from AT&#x26;T anticipated the sticker shock and sent a short, well-made video that explained all of  my individual charges. Now this wasn’t the same as a more general bill walkthrough (like the one Jellyvision used to have on their work page). This one had <em>my charges</em> on it. It was about me. Undoubtedly, this has been templatized, but as a developer, I appreciate the fact that they went that extra step to explain my particular bill situation.</p>\n<h2>Steal this Idea</h2>\n<p>So how might you use this idea? If you do any sort of client-facing training, whether that’s how to use the LMS or how to use the software or other product your organization has developed, think about the impact that a follow up email with a personalized job-aid could have. How might you create a reusable template that you could send out to new users? What questions or concerns can you address, right off the bat? Try creating a resource that provides an initial customer service experience for your organization, before folks have to email or get on the phone with you. This is a proactive approach that has anticipated a common problem (sticker shock, confusion over the first bill) and pre-empted it, providing a positive initial experience. In fact, I was just discussing this idea at work, where we are facing an issue of not having enough staff to meet demand for training around basic instructional design principles and our LMS. I suggested that we needed to create an initial resource for campus groups looking to learn more about both of these issues, to provide that first experience. For folks who are just looking for some basic facts and how-tos, the resource would give them exactly what they need, on demand. For others who have more questions, they could come in and see us. This approach is sustainable and scalable, helping us to meet the needs of the hundreds of faculty members we serve. In the light of this, I could also see us going that extra mile, particularly after a workshop or consultation, and sending a personalized job aid, tailored for that particular person’s course idea. Do any of you do anything like this currently? Have you received a really good piece of instructional design lately?</p>","frontmatter":{"date":"October 19, 2015","path":"/blog/steal-this-idea-personalized-job-aids","title":"Steal this Idea: Personalized Job Aids","tags":["job aid","microlearning","personalization","video"],"categories":["Design","Inspiration"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/Steal-This-Idea-99bb948d3852c91cf96b0d92457ba6c9-81d39.png","srcSet":"/static/Steal-This-Idea-99bb948d3852c91cf96b0d92457ba6c9-9a8d1.png 203w,\n/static/Steal-This-Idea-99bb948d3852c91cf96b0d92457ba6c9-c53f9.png 405w,\n/static/Steal-This-Idea-99bb948d3852c91cf96b0d92457ba6c9-81d39.png 810w,\n/static/Steal-This-Idea-99bb948d3852c91cf96b0d92457ba6c9-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

